	module.exports = {"data":{"markdownRemark":{"html":"<p>授業では、博士課程の学生が、中国の政治発展のモード選択について議論し、歴史的発展の観点から中国の政治発展の条件を決定し、中国の政治発展の可能性と不可能性を議論する。議論は圧倒的でした。この問題は、アヘン戦争以来、中国で未完成の問題であった。物理的な使用のための闘争は本質的にパターン紛争の早期相違であり、重大なことはモデルを選ぶことの問題です。 20世紀前半には、この問題は引き続き議論されており、多くの派閥が存在すると言えます。このような \"ナショナリズム\"命題 \"村建設派閥\"など、</p>\n<p>「社会ダーウィニズム」の考え方など。国民党と共産党の間の闘争は、中国の開発モデルの闘争でもある。共産党は、人気のある戦略と革新戦略を実行し、人々の心を勝ち取って勝利する。勝利後、毛沢東の戦略は政治的な成果を上げ、成功と失敗で発展させることでした。鍵は、経済が完全に発展していないことです。 Deng Xiaopingは、「文化革命」の後に新しい行と開発戦略を選んだ。これは、社会的生産力の発展を促進することに焦点を当てて中国の近代化建設を促進することである。中国の発展の観点から、これまでにない成果をあげています。これは発展モデルでもあり、中国の発展のためのより適切なモデルを見つけました。中国の政治発展はこの一般的なパターンの下で検討されるべきである。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0166.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0166.md","frontmatter":{"title":"10月24日 月曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0166.md absPath of file >>> MarkdownRemark"}}

